(()=>{
    'use strict';

    {
        class Animations{
        
            _animate({timing, draw, duration}, event) {
        
                let start = performance.now();
              
                requestAnimationFrame(function animate(time) {
                  // timeFraction goes from 0 to 1
                  let timeFraction = (time - start) / duration;
                  if (timeFraction > 1) timeFraction = 1;
              
                  // calculate the current animation state
                  let progress = timing(timeFraction);
              
                  draw(progress); // draw it
              
                  if (timeFraction < 1) {
                    requestAnimationFrame(animate);
                  } else if(event){
                    document.dispatchEvent(event);
                  }
              
                });
            }
        }
        
        
        class Box{
            constructor(box){
        
                class Door{
                    constructor(door, left = true){
                        this.left = left;
                        this.dom = door;
                        this.transform = {
                            translateZ: left ? 0 : -100,
                            rotateX: left ? 270 : 90 //- closed 270 / 90 (open = 60 / 300) 
                        }
        
                        this.rebuildDom();
                    }
                    
                    _constructTransform(){
                        return `translateZ(${this.transform.translateZ}px) rotateX(${this.transform.rotateX}deg)`;
                    }
        
                    rebuildDom(){
                        this.dom.style.transform = `${this._constructTransform()}`;
                    }
        
                    open(){
                        this.transform.rotateX = this.left ? 60 : 300;
                        this.rebuildDom();
                    }
        
                    close(){
                        this.transform.rotateX = this.left ? 270 : 90;
                        this.rebuildDom();
                    }
                }
                
        
        
                this.dom = box;
                this.opened = false;
                this.transform = {
                    rotateX : 0,    //-20
                    rotateY : -80,
                    rotateZ :  0,
                    scaleY :  1,
                    scaleX :  1,
                    scaleZ :  1,
                    translateX :  0,
                    translateY :  window.innerHeight * 0.4,
                    translateZ :  0,
                };
                this.leftDoor = new Door(this.dom.querySelector('.box__side--top-left'));
                this.rightDoor = new Door(this.dom.querySelector('.box__side--top-right'), false);
        
                this.rebuildDom();
            }
        
            rebuildDom(){
        
                this.dom.style.transform = `${this._constructTransform()}`;
            }
        
            open(){
                this.leftDoor.open();
                this.rightDoor.open();
            }
        
            close(){
                this.leftDoor.close();
                this.rightDoor.close();
            }
        
            jump(){
                
                let speed = 20;
        
                this.dom.addEventListener('preparedToJump', function (e){
                    // console.log('prepared to jump!');
        
                    accelerate.apply(this);
        
                }.bind(this), {once: true});
        
                this.dom.addEventListener('accelerated', function (e){
                    // console.log('accelerated!');
                    flyUp.apply(this);
                    this.dom.style.transformOrigin = "center center -50px";
        
                }.bind(this), {once: true});
        
                this.dom.addEventListener('flyedUp', function (e){
                    // console.log('flyedUp!');
        
                    this.dom.style.transformOrigin = "";
                    fallDown.apply(this);
        
                }.bind(this), {once: true});
        
                this.dom.addEventListener('falledDown', function (e){
                    // console.log('falledDown!');
        
                    grounding.apply(this);
        
                }.bind(this), {once: true});
        
                this.dom.addEventListener('grounded', function (e){
                    // console.log('grounded!');
        
                    groundSwing.apply(this);
        
                }.bind(this), {once: true});
        
                this.dom.addEventListener('groundSwinged', function (e){
                    // console.log('All Done!');
        
                }.bind(this), {once: true});
        
                
                prepareToJump.apply(this);
        
        
                // functions
        
        
                function groundSwing(){
        
        
                    let timing  = function (timeFraction){
        
                            let x = 1.5;
        
                            timeFraction = 1 - timeFraction;
                            //linear
                            return Math.pow(2, 10 * (timeFraction - 1)) * Math.cos(20 * Math.PI * x / 3 * timeFraction);
                        },
        
                        draw = (progress)=>{this._deformate(1 - progress);};
        
        
                    this._animate({
                        timing: timing,
                        draw,
                        duration: 100 * speed,
                    }, new Event('groundSwinged'));
                }
        
        
                function grounding(){
        
                    
                    let timing  = function (timeFraction){
        
                        return 1-(--timeFraction)*timeFraction*timeFraction*timeFraction;
                    },
        
                    draw = (progress)=>{this._deformate(1 - progress);};
        
        
                    this._animate({
                        timing: timing,
                        draw,
                        duration: 10 * speed,
                    }, new Event('grounded'));
                }
        
        
        
                function fallDown(){
        
                    let timing  = function (timeFraction){
        
                            return timeFraction*timeFraction*timeFraction;
                        },
        
                        draw = (progress)=>{this._translateY(1 - progress);};
        
        
                    this._animate({
                        timing: timing,
                        draw,
                        duration: 50 * speed,
                    }, new Event('falledDown'));
                }
        
        
                function flyUp(){
        
                    let timing  = function (timeFraction){
        
                            return (--timeFraction)*timeFraction*timeFraction+1;
                        },
        
                        draw = (progress)=>{
                            this._translateY(progress);
                        };
        
        
                    this._animate({
                        timing: timing,
                        draw,
                        duration: 70 * speed,
                    }, new Event('flyedUp'));
                }
        
        
                function accelerate(){
        
                    let max = -10;
        
                    let timing  = function (timeFraction){
        
                            let x = 1.5;
        
                            timeFraction = 1 - timeFraction;
                            //linear
                            return Math.pow(2, 10 * (timeFraction - 1)) * Math.cos(20 * Math.PI * x / 3 * timeFraction);
                        },
        
                        draw = (progress)=>{
                            this._deformate(1 - progress);
                           
                            if(1 - progress <= max || 1 - progress >= 1){
                                this.dom.dispatchEvent(new Event('accelerated'));
                            } else{
                                max = 1 - progress;
                            }
                        };
        
        
                    this._animate({
                        timing: timing,
                        draw,
                        duration: 100 * speed,
                    });
                }
        
        
                function prepareToJump(){
        
                    let timing  = function (timeFraction){
        
                            return timeFraction*(2-timeFraction);
                        },
        
                        draw = (progress)=>{this._deformate(1 - progress);};
        
        
                    this._animate({
                        timing: timing,
                        draw,
                        duration: 50 * speed,
                    }, new Event('preparedToJump'));
                }
            }
        
            _constructTransform(){
                return `translateX(${this.transform.translateX}px) translateY(${this.transform.translateY}px) translateZ(${this.transform.translateZ}px) rotateX(${this.transform.rotateX}deg) rotateY(${this.transform.rotateY}deg) rotateZ(${this.transform.rotateZ}deg) scaleY(${this.transform.scaleY}) scaleX(${this.transform.scaleX}) scaleZ(${this.transform.scaleZ})`;
            }
        
            _deformate(sc){ //sc (scale) 1 - scale = 1; 0 - min deformation; 2 - max deformation
        
                let minDef  = 0.6;
        
                    sc = minDef + (1 - minDef) * sc
        
                let scaleNew = {
                    y: sc,
                    x: 1 + (1 - sc)**2 * (sc > 1 ? -1 : 1),
                    z: 1 + (1 - sc)**2 * (sc > 1 ? -1 : 1),
                }
        
                this.transform.scaleX = scaleNew.x;
                this.transform.scaleY = scaleNew.y;
                this.transform.scaleZ = scaleNew.z;
        
                this.rebuildDom();
            }
        
            _translateY(y){ // 0 - 1 amount
        
                let maxY = window.innerHeight * 0.2,
                    minY = -window.innerHeight * 0.4;
        
                y = Math.abs(maxY - minY) * y + minY;
        
                this.transform.translateY = -y;
        
                this.rebuildDom();
            }
        
            _animate({timing, draw, duration}, event) {
        
                let start = performance.now();
              
                requestAnimationFrame(function animate(time) {
                  // timeFraction goes from 0 to 1
                  let timeFraction = (time - start) / duration;
                  if (timeFraction > 1) timeFraction = 1;
              
                  // calculate the current animation state
                  let progress = timing(timeFraction);
              
                  draw(progress); // draw it
              
                  if (timeFraction < 1) {
                    requestAnimationFrame(animate.bind(this));
                  } else if(event){
                    this.dom.dispatchEvent(event);
                  }
              
                }.bind(this));
            }
        }
        
        let box = new Box(document.getElementById('skillsBox'));
        
        // setTimeout(()=>{box.open()}, 2000);
        
        // setTimeout(()=>{box.close()}, 4000);
        
        box.jump();
        
        // box._deformate(1.2);
        
        
        let interval = setInterval(()=>{
        
            box.jump();
        
        }, 7000);
    }

    {
        // Swipe handler
        
        document.addEventListener('touchstart', handleTouchStart, false);        
        document.addEventListener('touchmove', handleTouchMove, false);
        
        let xDown = null;                                                        
        let yDown = null;
        let alignmentToNav = true;
        
        function getTouches(evt) {
        return evt.touches ||             // browser API
                evt.originalEvent.touches; // jQuery
        }                                                     
        
        function handleTouchStart(evt) {
            const firstTouch = getTouches(evt)[0];                                      
            xDown = firstTouch.clientX;                                      
            yDown = firstTouch.clientY;        
        };                                                
        
        function handleTouchMove(evt) {
            if ( ! xDown || ! yDown ) {
                return;
            }
        
            let xUp = evt.touches[0].clientX;                                    
            let yUp = evt.touches[0].clientY;
        
            let xDiff = xDown - xUp;
            let yDiff = yDown - yUp;
        
            alignmentToNav = (xDown + xUp)*4/3 > window.innerWidth ? true : false;
            alignmentToNav = xUp < innerWidth/4 ? false : alignmentToNav;
        
            if ( Math.abs( xDiff ) > Math.abs( yDiff ) ) {/*most significant*/
                if(!alignmentToNav) {
                    if ( xDiff > 0 ) {
                        /* left swipe */
                        document.querySelector('#touch').checked = false;
                    } else {
                        /* right swipe */
                        document.querySelector('#touch').checked = true;
                    }  
                } else {
                    if ( xDiff > 0 ) {
                        /* left swipe */
                        document.querySelector('#nav').checked = true;
                    } else {
                        /* right swipe */
                        document.querySelector('#nav').checked = false;
                    }  
                }                     
            } 
            else {
                if ( yDiff > 0 ) {
                    /* up swipe */ 
                    // document.querySelector('#touch').checked = false; classList.remove('full-view');
                } else { 
                    /* down swipe */
                    // document.querySelector('#touch').checked = false; classList.remove('full-view');
                }                                                                 
            }
            /* reset values */
            xDown = null;
            yDown = null;                                             
        };
    }

    {
        
        addTimeToEmployments();
        
        
        // functions
        
        
        function addTimeToEmployments(){
            let jobs = document.querySelectorAll('.job'),
                rembud = jobs[0].querySelector('.job__time'),
                cpi = jobs[1].querySelector('.job__time'),
                // boeing = jobs[2].querySelector('.job__time'),
                now = new Date(),
                rembudStartDate =  new Date(2018, 6),
                cpiStartDate = new Date(2017, 10);
                // boeingStartDate = new Date(2017, 6);
        
            rembud.innerHTML = `working within ${getTimeStringFromTo(rembudStartDate, now)}`;    
            cpi.innerHTML = `sarted ${getTimeStringFromTo(cpiStartDate, now)} ago`;  
            // boeing.innerHTML = `worked ${getTimeStringFromTo(boeingStartDate, now)} ago`;     
        }
        
        
        function getTimeStringFromTo(dateObjFrom, dateObjTo = new Date()){
        
            let years = 0,
                months = 0,
                days = 0,
                from = new Date(dateObjFrom.getTime()),
                to = new Date(dateObjTo.getTime());
        
            while(from.getDate() != to.getDate()){
                to.setDate(to.getDate()-1);
                ++days;
            }
            while(from.getMonth() != to.getMonth()){
                to.setMonth(to.getMonth()-1);
                ++months;
            }
            while(from.getFullYear() != to.getFullYear()){
                to.setFullYear(to.getFullYear()-1);
                ++years;
            }
            return `${years > 0 ? years + ' year'+ (years == 1 ? " " : "s ") : ''}${months > 0 ?( months + ' month' + (months == 1 ? " " : "s ")) : ''}`;
        }
    }

    {
        let imgs = document.querySelectorAll('.poster__img');
        // console.log(imgs.length);
        imgs.forEach( function (img){
            let imgClass = ((img.naturalWidth / img.naturalHeight) < 1.375) ? 'poster__img--tall' : 'poster__img--wide';
            // console.log((img.naturalHeight / img.naturalWidth));
            img.classList.add(imgClass);
        });
    }

    {
        
        let inputs = document.querySelectorAll('.message__input');
        inputs.forEach(function(input) {
            input.addEventListener('input', function() {
                // console.log(input.value);
                // console.log(input.value.length);
                if (input.value.length) {
                    input.classList.add('filled');
                } else {
                    input.classList.remove('filled');
                }
            });
        });
        
        // let btn = document.querySelector('#contactsSubmit');
        // let form = document.querySelector('.contacts');
        
        // btn.addEventListener('click', function() {
        //     form.classList.add('validated');
        // });
    }

    {
        myCarosel();
        
        function myCarosel(){
        
            let box = document.getElementById('portfolio').querySelector('.portfolio-slider'),
            slides = box.querySelectorAll('.portfolio-slider>div');
        
            // slides.forEach(slide=>{
            //     slide.style.display = "inline-block";
            // });
        
            let width = slides[0].offsetWidth;
        
            let h = width/2 * 1 / Math.tan(180 / slides.length * Math.PI / 180);
        
            box.style.transform = `rotateY(0deg) translateZ(-${h}px)`;
        
            slides.forEach((slide, i)=>{
                slide.style.cssText = `
                    transform: rotateY(${360 / slides.length * i}deg)  translateZ(${h}px);
                `;
            });
        
        
            //left btn
        
            let left = document.getElementById('sliderRotateLeft'),
                right = document.getElementById('sliderRotateRight'),
                rotationAngle = 0,
                rotationStep = 360 / slides.length;
        
        
            left.onclick = ()=>{
                rotationAngle -= rotationStep;
                box.style.transform = "translateZ(-" + h + "px) rotateY(" + rotationAngle + "deg) ";
            };
        
            right.onclick = ()=>{
                rotationAngle += rotationStep;
                box.style.transform = "translateZ(-" + h + "px) rotateY(" + rotationAngle + "deg) ";
            };
        }
    }
})();

//callbacks


{
    let sections = document.querySelectorAll('section');
    
    let hui = document.styleSheets[0].rules || document.styleSheets[0].cssRules;
    
    let styleBySelector = {};
    for (let i=0; i<hui.length; i++)
        styleBySelector[hui[i].selectorText] = hui[i].style;
    
    
    let scrollTimeout;
    
    window.addEventListener('scroll', (e)=>{
    
        clearTimeout(scrollTimeout);
        scrollTimeout = setTimeout(changeScrollbarColor, 1000);
    });
    
    
    function changeScrollbarColor(){
    
    
        let height = document.documentElement.clientHeight;
    
        let scrollHeight = window.pageYOffset;
    
        let sectionCounter = 0;
    
        while(scrollHeight >= 0){
            scrollHeight -= sections[sectionCounter].scrollHeight;
            sectionCounter++;
        }
    
        if(sections[sectionCounter - 1].getBoundingClientRect().bottom > height * 0.4){
            styleBySelector["body::-webkit-scrollbar-thumb"].backgroundColor = sections[sectionCounter - 1].dataset.scrollbar;
        } else {
            styleBySelector["body::-webkit-scrollbar-thumb"].backgroundColor = sections[sectionCounter].dataset.scrollbar;
        }
    
    }
}